import {
    HomeOutlined,
    ProfileOutlined,
    PictureOutlined,
    LockOutlined,
    SmileOutlined,
    TeamOutlined,
    UserSwitchOutlined,
    DatabaseOutlined,
    UnorderedListOutlined
} from '@ant-design/icons';
import Home from './pages/home';
import Artical from './pages/artical';
import Jotting from './pages/jotting';
import Picture from './pages/picture';
import User from './pages/authority/user';
import Role from './pages/authority/role';
import Resource from './pages/authority/resource';
import Journal from './pages/journal';

const menuList = [
    {
        title: '',
        key: '/admin/home',
        component: Home,
        icon: <HomeOutlined />,
    },
    {
        title: '',
        key: '/admin/artical',
        component: Artical,
        icon: <ProfileOutlined />,
    },
    {
        title: '',
        key: '/admin/jotting',
        component: Jotting,
        icon: <SmileOutlined />,
    },
    {
        title: '',
        key: '/admin/picture',
        component: Picture,
        icon: <PictureOutlined />
    },
    {
        title: '',
        key: '/admin/authority',
        icon: <LockOutlined />,
        children: [
            {
                title: '',
                key: '/admin/authority/user',
                component: User,
                icon: <TeamOutlined />
            },
            {
                title: '',
                key: '/admin/authority/role',
                component: Role,
                icon: <UserSwitchOutlined />
            },
            {
                title: '',
                key: '/admin/authority/resource',
                component: Resource,
                icon: <DatabaseOutlined />
            }
        ]
    },
    {
        title: '',
        key: '/admin/log',
        component: Journal,
        icon: <UnorderedListOutlined />,
    }
];

export default menuList;