import { useState, useEffect, useRef } from 'react';
import { Table, Space, Button, message, Modal } from 'antd';
import { reqQueryJournals, reqDeleteJournal } from '../../api/journal';
import LogModal from './logModal';
import './index.less';

const { confirm } = Modal;

const Journal = () => {
    const [logModalVisible, setLogModalVisible] = useState(false);
    const [journalListSelectedKeys, setJournalListSelectedKeys] = useState([]);
    const [modalCode, setModalCode] = useState(0);
    const [dataSource, setDataSource] = useState([]);
    const [recordData, setRecordData] = useState({});
    const [loading, setLoading] = useState(true);
    const [pageInfo, setPageInfo] = useState({ current: 1, pageSize: 10 });
    const [total, setTotal] = useState(100);

    const columns = [
        {
            title: '',
            dataIndex: '_id',
            width: '5%',
            align: 'center',
            render: (...args) => {
                return args[2] / 1 + 1
            }
        },
        {
            title: '',
            dataIndex: 'abstract',
            key: 'abstract',
            ellipsis: true,
            width: '20%'
        },
        {
            title: '',
            dataIndex: 'content',
            key: 'content',
            ellipsis: true
        },
        {
            title: '',
            dataIndex: 'createTime',
            key: 'createTime',
            align: 'center',
            width: '15%'
        },
        {
            title: '',
            dataIndex: 'updateTime',
            key: 'updateTime',
            align: 'center',
            width: '15%'
        },
        {
            title: '',
            key: 'action',
            render: (text, record) => (
                <Space size="middle">
                    <a onClick={() => { handleRead(record) }}></a>
                    <a onClick={() => { handleUpdate(record) }}></a>
                </Space>
            ),
            align: 'center',
            width: '15%'
        },
    ];

    // 
    const queryJournals = () => {
        const params = {
            pageInfo
        };
        reqQueryJournals(params).then((data) => {
            const { result, total } = data;
            setLoading(false);
            setDataSource(result);
            setTotal(total)
        }).catch((err) => {
            message.error('');
        });
    }

    // 
    const handleCreate = () => {
        setRecordData({});
        setModalCode(0);
        setLogModalVisible(!logModalVisible);
    }

    // 
    const handleRead = (record) => {
        setModalCode(1);
        setRecordData(record);
        setLogModalVisible(!logModalVisible);
    }

    // 
    const handleUpdate = (record) => {
        setModalCode(2);
        setRecordData(record);
        setLogModalVisible(!logModalVisible);
    }

    // 
    const handleDelete = () => {
        const params = {
            ids: journalListSelectedKeys
        };
        confirm({
            title: '',
            content: '',
            okText: '',
            cancelText: '',
            onOk: () => {
                return reqDeleteJournal(params).then(() => {
                    message.success('');
                    queryJournals();
                }).catch(() => {
                    message.error('');
                })
            },
        })
    }

    const handlePageInfoChange = (page, pageSize) => {
        setPageInfo({
            pageNo: page,
            pageSize: pageSize
        });
        setLoading(true);
    }

    // 
    useEffect(() => {
        queryJournals();
    }, [])

    // 
    useEffect(() => {
        queryJournals();
    }, [pageInfo])

    const rowSelection = {
        selectedRowKeys: journalListSelectedKeys,
        onChange: (selectedKeys) => {
            setJournalListSelectedKeys(selectedKeys);
        }
    }

    // 
    const pagination = {
        current: pageInfo.current,
        pageSize: pageInfo.pageSize,
        total: total,
        showSizeChanger: true,
        onChange: handlePageInfoChange
    }

    return (
        <div className="journal">
            <div className='journal-btns'>
                <Space>
                    <Button type='primary' onClick={handleCreate}></Button>
                    <Button type='primary' onClick={handleDelete}></Button>
                </Space>
            </div>
            <Table
                bordered
                columns={columns}
                dataSource={dataSource}
                rowKey={record => record._id}
                rowSelection={rowSelection}
                pagination={pagination}
                loading={loading}
            />
            {
                logModalVisible ? <LogModal
                    modalCode={modalCode}
                    logModalVisible={logModalVisible}
                    setLogModalVisible={setLogModalVisible}
                    queryJournals={queryJournals}
                    recordData={recordData}
                /> : null
            }
        </div>
    )
}

export default Journal;