import { Modal, Form, Input, Button, Space, message, DatePicker } from 'antd';
import moment from 'moment';
import { reqCreateJournal, reqUpdateJournal } from '../../api/journal';

const FormItem = Form.Item;
const { TextArea } = Input;
const titleArr = ['', '', ''];
const formLayout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 18 }
}

const LogModal = (props) => {
    const {
        modalCode,
        logModalVisible,
        setLogModalVisible,
        queryJournals,
        recordData
    } = props;

    const [form] = Form.useForm();

    const handleCancel = () => {
        setLogModalVisible(false);
    }

    const handleConfirm = () => {
        const values = form.getFieldsValue();
        if (modalCode === 0) {
            const params = {
                ...values,
                createTime: moment().format('YYYY-MM-DD HH:mm:ss')
            };
            console.log(params);
            return reqCreateJournal(params).then((res) => {
                const { msg } = res;
                setLogModalVisible(false);
                message.success(msg);
                queryJournals();
            }).catch((err) => {
                message.error(err);
            });
        } else if (modalCode === 2) {
            const params = {
                ...values,
                id: recordData._id,
                updateTime: moment().format('YYYY-MM-DD HH:mm:ss')
            };
            reqUpdateJournal(params).then((res) => {
                const { msg } = res;
                setLogModalVisible(false);
                message.success(msg);
                queryJournals();
            });
        } else {
            setLogModalVisible(false);
        }
    }

    return (
        <Modal
            title={titleArr[modalCode]}
            width={800}
            visible={logModalVisible}
            onCancel={() => { setLogModalVisible(false) }}
            footer={null}
        >
            <Form
                {...formLayout}
                form={form}
            >
                <FormItem
                    label=''
                    name='abstract'
                    initialValue={recordData.abstract ? recordData.abstract : null}
                    rules={[
                        {
                            required: true,
                            message: '',
                        },
                    ]}
                >
                    <Input disabled={modalCode === 1 ? true : false} />
                </FormItem>
                <FormItem
                    label=''
                    name='content'
                    initialValue={recordData.content ? recordData.content : null}
                >
                    <TextArea
                        showCount
                        autoSize={{ minRows: 6 }}
                        maxLength={200}
                        disabled={modalCode === 1 ? true : false}
                    />
                </FormItem>
                <FormItem
                    wrapperCol={{ span: 18, offset: 4 }}
                >
                    <Space>
                        <Button type='primary' onClick={handleConfirm}></Button>
                        <Button type='primary' onClick={handleCancel}></Button>
                    </Space>
                </FormItem>
            </Form>
        </Modal>
    )
}

export default LogModal;