import ajax from './ajax';

const baseURL = '/admin-blog-cms/journal';

// 
export const reqQueryJournals = (params) => {
    return new Promise((resolve, reject) => {
        ajax.postJson(baseURL + '/queryJournalList', params).then((res) => {
            resolve(res.data);
        }).catch((err) => {
            reject(err);
        })
    })
}

// 
export const reqCreateJournal = (params) => {
    return new Promise((resolve, reject) => {
        ajax.postJson(baseURL + '/createJournal', params).then((res) => {
            resolve(res.data);
        }).catch((err) => {
            reject(err)
        })
    })
}

// 
export const reqUpdateJournal = (params) => {
    return new Promise((resolve, reject) => {
        ajax.postJson(baseURL + '/updateJournal', params).then((res) => {
            resolve(res.data);
        }).catch((err) => {
            reject(err)
        })
    })
}

// 
export const reqDeleteJournal = (params) => {
    return new Promise((resolve, reject) => {
        ajax.getParams(baseURL + '/deleteJournal', params).then((res) => {
            resolve(res.data);
        }).catch((err) => {
            reject(err)
        })
    })
}