import { Form, Input, Button, Space, message } from 'antd';
import { Redirect } from 'react-router-dom';
import {
	MailOutlined,
	LockFilled,
} from '@ant-design/icons';
import { reqLogin } from '../../api/index';
import storage from '../../utils/storage';
import './index.less';
import logo from '../../assets/images/loginLogo.jpg';

const FormItem = Form.Item;

const Login = (props) => {
	// 
	const user = storage.getUser();

	const handleSubmit = (formData) => {
		reqLogin(formData).then((res) => {
			const { status, data } = res;
			if (status === 200) {
				storage.saveUser(data.user);
				props.history.replace('/admin');
				message.success("");
			} else {
				message.error('');
			}
		})
	}

	return (user && user.id) ? <Redirect to='/admin' /> :
		(
			<div className='login'>
				<div className="login-header">
					<div className="main">
						<img src={logo} alt="." className='login-logo' />
						<p className="login-title"></p>
					</div>
				</div>
				<div className="login-content">
					<h2></h2>
					<Form
						onFinish={handleSubmit}
					>
						<FormItem
							label=""
							name="email"
							labelCol={{ span: 6 }}
							wrapperCol={{ span: 14 }}
							rules={[
								{ required: true, message: '' },
								{ pattern: /^[a-zA-Z0-9_\-]{2,}@[a-zA-Z0-9_\-]{2,}(\.[a-zA-Z0-9_\-]+){1,2}$/, message: '' }
							]}
						>
							<Input prefix={<MailOutlined style={{ color: '#000', fontSize: '18px' }} />} placeholder="Email" />
						</FormItem>
						<FormItem
							label=""
							name="password"
							labelCol={{ span: 6 }}
							wrapperCol={{ span: 14 }}
							rules={[
								{ required: true, message: '' },
								{ min: 6, message: '6' },
								{ max: 12, message: '12' }
							]}
						>
							<Input prefix={<LockFilled style={{ color: '#000', fontSize: '18px' }} />} placeholder="Password" />
						</FormItem>
						<FormItem wrapperCol={{ offset: 6, span: 16 }}>
							<Space>
								<Button type="primary" htmlType="submit" ghost></Button>
								<Button type="primary" ghost></Button>
								<Button type="primary" ghost></Button>
							</Space>
						</FormItem>
					</Form>
				</div>
			</div>
		)
};

export default Login;